.footer {
    background-color: rgb(249, 252, 237);
    padding-inline: 4%;
    padding-block: 48px 32px;
    .list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 48px;
  
      .list_item {
        min-width: 280px;
        list-style: none;
        display: flex;
        flex-direction: column;
        gap: 16px;
        .item {
          &:first-child {
            font-size: var(--title-font-three);
            font-weight: 600;
            img {
              width: 90%;
              max-width: 200px;
              mix-blend-mode: multiply;
            }
          }
          a {
            color: var(--text-color);
            transition: 0.2s;
            &:hover {
              color: var(--main-color);
            }
          }
  
          .soical_links {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 32px;
            list-style: none;
            img {
              width: 48px;
              height: 48px;
              object-fit: contain;
            }
          }
        }
        &:last-child {
          padding-left: 5%;
        }
      }
    }
    p {
      text-align: center;
      margin-top: 32px;
    }
  }