.footer {
  background-color: rgb(249, 252, 237);
  padding-inline: 4%;
  padding-block: 48px 32px;
}
.footer .list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 48px;
}
.footer .list .list_item {
  min-width: 280px;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.footer .list .list_item .item:first-child {
  font-size: var(--title-font-three);
  font-weight: 600;
}
.footer .list .list_item .item:first-child img {
  width: 90%;
  max-width: 200px;
  mix-blend-mode: multiply;
}
.footer .list .list_item .item a {
  color: var(--text-color);
  transition: 0.2s;
}
.footer .list .list_item .item a:hover {
  color: var(--main-color);
}
.footer .list .list_item .item .soical_links {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 32px;
  list-style: none;
}
.footer .list .list_item .item .soical_links img {
  width: 48px;
  height: 48px;
  -o-object-fit: contain;
     object-fit: contain;
}
.footer .list .list_item:last-child {
  padding-left: 5%;
}
.footer p {
  text-align: center;
  margin-top: 32px;
}/*# sourceMappingURL=Footer.css.map */